@import "./logo/logo";
@import "./main-menu/main-menu";
@import "./search/search";
@import "./phone/phone";
@import "./calc-btn/calc-btn";

.header {
    border-bottom: 1px solid #E1E1E1;
    box-shadow: 1px 0px 10px rgba(0, 0, 0, 0.1);

    & .container {
        display: flex;
        justify-content: space-between;
        height: 125px; }

    &__container {
        display: flex; }

    &__center {
        display: flex;
        align-items: center;
        justify-content: center; }

    &__right {
        width: 161px;
        font-family: $font-family-h; } }


@include media-breakpoint-up(xl) {
    .header {
        &__container {
            gap: 80px; }

        &__center {
            gap: 40px;
            padding: 0 24px; } } }

@include media-breakpoint-only(xl) {
    .header {
        &__container {
            gap: 34px; }

        &__center {
            gap: 20px;
            padding: 0 20px; } } }


@include media-breakpoint-only(lg) {
    .header {
        &__container {
            gap: 34px; }

        &__center {
            gap: 20px;
            padding: 0 54.5px;

            & .btn {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 60px;
                height: 50px;
                background: $main-blue;
                border-radius: 0px 5px;
                color: $white; } } } }

@include media-breakpoint-down(lg) {
    .header {
        &__container {
            flex-direction: column-reverse;

            & .btn {
                display: flex;
                justify-content: center;
                align-items: center;
                background: $main-blue;
                border-radius: 0px 5px;
                color: $white; } }

        &__center {
            gap: 10px; }

        &__right {
            width: fit-content; } } }

@include media-breakpoint-only(md) {
    .header {
        & .container {
            padding: 0 5px; }
        &__container {
            & .btn {
                width: 50px;
                height: 45px;
                padding: 0; } }

        &__center {
            & .icon {
                width: 28px !important;
                height: 28px !important; } }

        &__phone a {
            font-size: 23px; } } }

@include media-breakpoint-down(md) {
    .header {
        &__container {
            & .btn {
                width: 40px;
                height: 35px;
                padding: 0; } }

        &__center {
            & .icon {
                width: 24px !important;
                height: 24px !important; } }

        &__phone a {
            font-size: 20px;
            letter-spacing: 0.01em; } } }
