.main-carousel {
    width: 100%;
    max-width: 1280px;
    margin: 0 auto;

    &__title {
        display: flex;
        justify-content: center;
        width: 100%;
        padding: 20px 0;
        background: $main-blue;
        border-width: 1px 0px;
        border-style: solid;
        border-color: #E1E1E;
        color: $white;

        & a {
            font-family: $font-family-h;
            font-weight: 500;
            font-size: 25px;
            color: $white;
            line-height: 100%;
            text-align: center;

            &:hover {
                text-decoration: underline; } } }

    &__control-btn {
        position: absolute;
        top: 149px;
        width: 70px;
        height: 70px;
        background: $main-blue;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

        &:hover {
            background: $main-red; } }

    & .carousel-control-prev {
        margin-left: 31.5px;
        padding-right: 5px; }

    & .carousel-control-next {
        margin-right: 31.5px;
        padding-left: 5px; } }

@include media-breakpoint-up(xxl) {
    .main-carousel {

        &__control-btn {
            top: 206px; } } }

@include media-breakpoint-down(lg) {
    .main-carousel {
        &__control-btn {
            top: 72.25px;
            width: 30px;
            height: 30px;

            & .carousel-control-next-icon {
                width: 10px;
                height: 10px; }

            & .carousel-control-prev-icon {
                width: 10px;
                height: 10px; } }

        & .carousel-control-prev {
            margin-left: 19.5px;
            padding-left: 5px; }

        & .carousel-control-next {
            margin-right: 19.5px;
            padding-right: 5px; } } }

@include media-breakpoint-down(md) {
    .main-carousel {
        &__title {
            padding: 11px 0;

            & a {
                font-size: 20px; } } } }
