.logo {
    width: fit-content;

    &__link {
        display: inline-block;

        img {
            width: 100%;
            height: 100%; } } }

@include media-breakpoint-up(md) {

    .logo {
        &__link {
            width: 210px;
            height: 44.06px; } } }

@include media-breakpoint-down(md) {

    .logo {
        &__link {
            width: 130px;
            height: 27.28px; } } }

