.main-menu-container {
    position: relative; }

@include media-breakpoint-up(xl) {
    .main-menu {
        display: flex;
        flex-direction: row;
        column-gap: 40px;

        &__link {
            font-size: 14px;

            &--focused {
                padding: 15px 0; } }

        & a {
            display: inline-block;
            font-family: $font-family-h;
            font-weight: 500;
            color: $main-blue-dark;
            letter-spacing: 0.01em;

            &:hover {
                color: $main-red;
                text-decoration: underline; } }


        & .dropdown-menu {
            padding: 30px;
            background-color: #FFFFFF;
            border: 1px solid #E1E1E1;
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
            border-radius: 0px 15px;

            & a {
                font-family: $font-family-base;
                font-size: 16px; }

            & li {
                width: max-content;
                list-style: inside;

                & a {
                    padding: 6px;
                    margin-left: -8px; } }

            & > li a {
                font-weight: 700; } }

        &__last-nesting {
            margin-left: 30px; } }

    .main-menu__dropdown:hover .dropdown-menu {
        display: block; } }

@include media-breakpoint-only(xl) {
    .main-menu {
        column-gap: 20px; } }

@include media-breakpoint-down(xl) {
    .main-menu {
        &__link {
            display: none; } }

    #mainMenuBtn {

        & .icon {
            width: 27px;
            height: 23px; } } }

@include media-breakpoint-down(xl) {
    .main-menu {
        position: absolute;
        display: none;
        top: 75px;
        left: 0px;
        width: 388px;
        padding: 10px 25px 20px;
        background: $white;
        border: 1px solid #E1E1E1;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.3);
        border-radius: 0px 15px;
        font-size: 16px;
        z-index: 3;

        &__link {
            display: inline-block;
            width: 100%;
            margin-top: 15px;
            padding-bottom: 5px;
            font-weight: 700;
            color: $main-blue;
            border-bottom: 1px solid $main-blue; }

        & .dropdown-menu {
            position: unset;
            display: flex;
            flex-direction: column;
            gap: 10px;
            padding: 10px 0 0 0;
            border: none;

            & li {
                list-style: inside;
                line-height: 26px;

                & a {
                    margin-left: -8px;
                    font-size: 16px;
                    color: #696969; } }

            & > li {
                font-weight: 700;
                margin-left: 19px; } }

        &__last-nesting {
            display: flex;
            flex-direction: column;
            gap: 10px;
            margin-left: 34px; } } }

@include media-breakpoint-only(md) {
    .main-menu {
        top: 57px;
        left: -265px;
        width: 440px; } }

@include media-breakpoint-down(md) {
    .main-menu {
        top: 50px;
        left: -160px;
        width: 300px; } }
