// Font
$font-family-base: 'PT Sans', sans-serif;
$font-family-h: 'Oswald', sans-serif;
$enable-smooth-scroll: false;

/**
 * Grid system
 * -----------------------------------------
 */

// Grid breakpoints
$grid-breakpoints: (
	xs: 0,
	sm: 320px,
	md: 480px,
	lg: 768px,
	xl: 992px,
	xxl: 1200px,
);

// Grid containers
$container-max-widths: (
	sm: 300px,
	md: 440px,
	lg: 688px,
	xl: 880px,
	xxl: 1060px,
);

// Grid columns
//
// Set the number of columns and specify the width of the gutters.

$grid-columns:                12;
$grid-gutter-width:           1.5rem;
$grid-row-columns:            6;

// Links
//$link-color: $blue;
//$link-decoration:                         none;
//$link-hover-color:                        darken($link-color, 15%);
//$link-hover-decoration:                   underline;
// Darken percentage for links with `.text-*` class (e.g. `.text-success`)
//$emphasized-link-hover-darken-percentage: 15%;


/**
 * Spacing
 * ---------------------------------------------
 */
$spacer: 1rem;
$spacers: (
	0: 0,
	1: ($spacer * .25),
	2: ($spacer * .5),
	3: $spacer,
	4: ($spacer * 1.5),
	5: ($spacer * 3),
	10: ($spacer * 0.625), // 10px
	15: ($spacer * 0.9375), // 15px
	20: ($spacer * 1.25), // 20px
	25: ($spacer * 1.5625), // 25px
	30: ($spacer * 1.875), // 30px
	40: ($spacer * 2.5), // 40px
	50: ($spacer * 3.125), // 50px
	60: ($spacer * 3.75), // 60px
	70: ($spacer * 4.375), // 70px
	80: ($spacer * 5), // 80px
	90: ($spacer * 5.625), // 90px
	100: ($spacer * 6.25) // 100px
);

// $negative-spacers: if($enable-negative-margins, negativify-map($spacers), null) !default;

/*
 * Color system
 * ---------------------------------------------
 */

$white:    #fff;
$gray-100: #f8f9fa;
$gray-200: #e9ecef;
$gray-300: #dee2e6;
$gray-400: #ced4da;
$gray-500: #adb5bd;
$gray-600: #6c757d;
$gray-700: #495057;
$gray-800: #343a40;
$gray-900: #212529;
$black:    #000;

// fusv-disable
$grays: (
	"100": $gray-100,
	"200": $gray-200,
	"300": $gray-300,
	"400": $gray-400,
	"500": $gray-500,
	"600": $gray-600,
	"700": $gray-700,
	"800": $gray-800,
	"900": $gray-900
);
// fusv-enable

$main-blue:		 #134D81;
$main-blue-dark: #023671;
$main-red: 		 #E31E24;

$blue:      #0d6efd;
$indigo:  #6610f2;
$purple:  #6f42c1;
$pink:    #d63384;
$red:     #dc3545;
$orange:  #fd7e14;
$yellow:  #ffc107;
$green:   #198754;
$teal:    #20c997;
$cyan:    #0dcaf0;

// scss-docs-start colors-map
$colors: (
	"blue":       $blue,
	"indigo":     $indigo,
	"purple":     $purple,
	"pink":       $pink,
	"red":        $red,
	"orange":     $orange,
	"yellow":     $yellow,
	"green":      $green,
	"teal":       $teal,
	"cyan":       $cyan,
	"white":      $white,
	"gray":       $gray-600,
	"gray-dark":  $gray-800
);
// scss-docs-end colors-map

$primary:       $main-blue;
$secondary:     $gray-600;
$success:       $green;
$info:          $cyan;
$warning:       $yellow;
$danger:        $red;
$light:         $gray-100;
$dark:          $gray-900;

// scss-docs-start theme-colors-map
$theme-colors: (
	"primary":    $primary,
	"secondary":  $secondary,
	"success":    $success,
	"info":       $info,
	"warning":    $warning,
	"danger":     $danger,
	"light":      $light,
	"dark":       $dark
);

/**
 * Buttons
 * --------------------------------------
 */
//$btn-border-radius: 0;
//$btn-border-radius-lg: 0;
//$btn-border-radius-sm: 0;

//$btn-font-size: 0.875rem;
//$btn-font-size-sm: 0.875rem;
//$btn-font-size-lg: 1.125rem;

/**
 * Utilities
 * -----------------------------------------------------
 */
@import "../../../node_modules/bootstrap/scss/functions";
@import "../../../node_modules/bootstrap/scss/variables";
@import "../../../node_modules/bootstrap/scss/maps";
@import "../../../node_modules/bootstrap/scss/utilities";

$utilities: map-merge(
	$utilities,
	(
		// Font weight
		"font-weight": map-merge(
				map-get($utilities, "font-weight"),
			(
				values: map-merge(
					map-get(map-get($utilities, "font-weight"), "values"),
					(medium: 600),
				),
			),
		),
		// Opacity
		"opacity": (
			property: opacity,
			responsive: true,
			values: (
				0: 0,
				25: .25,
				50: .5,
				75: .75,
				100: 1,
			)
		),
		// Font-size
		"font-size": (
			property: "font-size",
			responsive: true,
			class: fz,
			values: (
				10: 10px,
				12: 12px,
				14: 14px,
				16: 16px,
				18: 18px,
			)
		),
		// Max-width
		"max-width": (
			property: max-width,
			responsive: true,
			class: mw,
			values: (
				25: 25%,
				50: 50%,
				75: 75%,
				100: 100%,
			)
		)
	)
);

$carousel-control-color:             #E1E1E1;
$carousel-control-opacity:           1;
$carousel-control-hover-opacity:     1;
