a {
    text-decoration: none; }

.row > * {
    width: unset;
    padding: 0px; }

::-webkit-scrollbar {
    width: 0px; }

.btn:focus,
.btn:active,
.btn:hover,
.btn:active:hover,
.btn:active:focus {
    border: none !important;
    outline: none !important;
    box-shadow: none !important; }

.container {
    --bs-gutter-x: 0rem; }

body {
    overflow-x: hidden; }
