@use "sass:math";

.icon {
    display: inline-block;
    width: 1em;
    height: 1em;
    fill: currentColor;
}

.icon-arrow-right {
    width: (33 / 33) * 1em;
}
.icon-calc {
    width: (28 / 28) * 1em;
}
.icon-flash {
    width: (10 / 15) * 1em;
}
.icon-list-menu {
    width: (27 / 23) * 1em;
}
.icon-search {
    width: (23 / 22) * 1em;
}
.icon-union {
    width: (83 / 44) * 1em;
}
