/* Titles
   @mixin ======================================================================== */
h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6 .h6 {
    margin: 0;
    font-family: $font-family-h;
    font-weight: 500; }


/**
 * Lists
 */
ul,
ol {
    list-style: none;
    margin: 0;
    padding: 0; }
