.headline {
    width: 100%;
    margin: 50px 0;

    & .container {
        display: flex;
        flex-direction: column;
        gap: 10px; }

    &__row {
        display: flex;
        justify-content: space-between;
        align-items: flex-start; }

    & h1 {
        color: $main-blue;
        line-height: 100%;
        letter-spacing: 0.012em;

        & span {
            color: #1C3143; } }

    &__experience-info {
        font-family: $font-family-h;
        font-weight: 500;
        font-size: 14px;
        color: $main-blue-dark;
        line-height: 100%;
        letter-spacing: 0.025em;

        & .icon {
            width: 9px;
            height: 14px;
            margin: -2px 7px 0 0; } }

    & .icon-union {
        width: 83px;
        height: 42.5px;
        color: $main-blue; } }

@include media-breakpoint-up(xl) {

    .headline {
        & h1 {
            font-size: 50px; } } }

@include media-breakpoint-only(lg) {

    .headline {
        & h1 {
            font-size: 43px; }

        &__experience-info {
            margin-top: 7px;
            font-size: 10px; }

        & .icon-union {
            width: 65px;
            height: 33px;
            margin-top: 4px; } } }

@include media-breakpoint-only(md) {

    .headline {
        & h1 {
            font-size: 25px; }

        &__experience-info {
            margin-top: 2px;
            font-size: 10px;
            letter-spacing: -0.015em; }

        & .icon-union {
            width: 58.1px;
            height: 29.75px; } } }

@include media-breakpoint-down(md) {
    .headline {
        margin: 30px 0;

        &__row:last-child {
            & h1 {
                letter-spacing: -0.025em; } }

        & h1 {
            font-size: 25px; }


        &__experience-info {
            display: none; }

        & .icon-union {
            display: none; } } }
