.footer {
    padding: 40px 0;
    background: $main-blue;
    color: #E1E1E1;

    & a {
        color: #E1E1E1; }


    &__content {
        display: flex;
        justify-content: center; }

    &__left {
        display: flex;
        flex-direction: column;
        align-items: flex-start; }

    &__logo {
        img {
            width: 335px; } }


    &__free-call {
        align-self: flex-end;
        width: 240px;
        margin-top: 11px;
        color: $white;

        & p {
            font-size: 14px;
            line-height: 130%; }

        & a {
            display: inline-block;
            margin-bottom: 14px;
            font-family: $font-family-h;
            font-weight: 500;
            font-size: 24px;
            line-height: 140%; } }

    &__ltd-itn {
        width: 100%;
        margin-top: 4px;
        padding: 17.5px 0;
        border-width: 1px 0;
        border-style: solid;
        border-color: $white;
        text-align: center;
        font-size: 14px;
        color: $white; }

    &__right {
        display: flex; }

    &__nav {
        width: 150px; }

    &__contacts {
        width: 150px; }

    &__title {
        font-size: 18px;
        line-height: 120%; } }


.link-group {
    margin-top: 31px;

    a:hover {
        text-decoration: underline; }


    &__main-link {
        font-size: 16px;
        font-weight: 700; }

    &__link {
        font-size: 14px;
        margin-top: 1px;
        line-height: 18px; } }

.contact-group {
    margin-top: 30px;

    p {
        font-size: 14px;
        line-height: 18px; } }

@include media-breakpoint-up(xxl) {
    .footer {
        &__right {}
        padding-bottom: 30px; } }

@include media-breakpoint-up(xl) {

    .footer {
        margin-top: 150px;
        &__content {
            column-gap: 140px; }

        &__right {
            gap: 100px; } } }


@include media-breakpoint-down(xl) {
    .footer {
        margin-top: 100px;
        &__content {
            column-gap: 30px; }

        &__right {
            gap: 30px; } } }

@include media-breakpoint-down(lg) {

    .footer {
        &__content {
            flex-direction: column;
            align-items: center;
            row-gap: 60px; }

        &__right {
            width: 400px;
            gap: 100px; } } }

@include media-breakpoint-down(md) {

    .footer {
        margin-top: 80px;
        &__left {
            width: 280px; }


        &__logo {
            img {
                width: 280px; } }

        &__free-call {
            width: 195px;
            margin-top: 14px; }

        &__right {
            width: 280px;
            gap: 20px; } } }


@include media-breakpoint-up(md) {

    .footer {
        &__left {
            flex-shrink: 0;
            width: 340px; } } }
