.page {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    text-align: left;
    min-width: 320px;
    width: 100%;
    transition: margin .2s ease;
    position: relative;
    min-height: 100vh;
    overflow-x: hidden;

    // Fix for IE11
    @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
        display: block; }

    &__content {
        flex: 1; } }
