.block {
    width: 100%;
    max-width: 1280px;
    margin-left: auto;
    margin-right: auto;

    &__header {
        display: flex;
        align-items: flex-start;
        width: 100%;

        &-left {
            flex-shrink: 0;
            padding-top: 7px; } }

    &__link {
        margin-top: 10px;
        font-weight: 700;
        font-size: 14px;
        line-height: 150%;
        color: $main-blue;

        &:hover {
            color: $main-red; } }

    &__title {
        font-size: 25px;
        line-height: 130%;
        color: $main-blue;
        letter-spacing: -0.01em;

        span {
            color: #1C3143; } }

    &__sub-title {
        margin-top: 10px;
        font-size: 23px;
        line-height: 23px;
        letter-spacing: 0.02em;
        color: #1C3143; }

    &__content {
        display: flex;
        flex-direction: row;
        width: 100%;

        &-left {
            flex-shrink: 0; } }

    &__product {
        & .block__content {
            flex-wrap: wrap; } }

    &__about-company {

        & .block {
            &__title {
                line-height: 126%;
                letter-spacing: 0.003em; }

            &__content {
                margin-top: 50px;

                & p {
                    margin-bottom: 22px;
                    font-size: 17px;
                    line-height: 170%;
                    color: #696969; } } } } }



.product-item {
    flex-grow: 1;

    &__main {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%; }

    &__image {
        max-height: 100%;
        max-width: 100%; }

    &__surface {
        position: absolute;
        width: 100%;
        height: 100%;
        padding: 10px; }

    &__top-text {
        font-size: 16px;
        line-height: 100%;
        letter-spacing: 0.03em; }

    & h3 {
        line-height: 16px; }

    &__title {
        font-size: 18px;
        line-height: 110%;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;

        &:hover {
            text-decoration: underline; } }

    &__description {
        height: 63px;
        font-size: 16px;
        line-height: 130%;
        letter-spacing: -0.005em;
        overflow: hidden; }

    &__bottom {
        display: flex;
        align-items: center;
        width: 100%;
        height: 93px;
        padding: 0 15px; }

    &__link {
        display: flex;
        align-items: center;
        gap: 15px;

        &:hover {
            text-decoration: underline; }


        & .icon {
            flex-shrink: 0;
            width: 33px;
            height: 33px; }

        & span {
            display: inline-block;
            height: 45px;
            font-family: 'Oswald';
            font-weight: 500;
            font-size: 15px;
            line-height: 140%;
            letter-spacing: 0.01em;
            overflow: hidden; } }



    &_dark {
        color: $white;

        & a {
            color: $white; }

        & .product-item {
            &__main {
                background: $main-blue-dark;
                border: 1px solid $main-blue-dark; }

            &__bottom {
                background: $main-blue-dark;
                border: 1px solid $main-blue-dark; } } }

    &_light {
        color: $main-red;

        & a {
            color: $main-blue; }

        & .product-item {
            &__main {
                border: 1px solid #E1E1E1; }

            &__bottom {
                border: 1px solid #E1E1E1; } } } }

.block__application {
    height: 412px;

    & .container {}

    & .block__content {
        position: absolute;
        display: flex;
        flex-direction: row;
        top: 0;
        left: 0; }

    & .splide__track {
        overflow: visible; } }

.application-item {

    &__main {
        color: $black;
        &:hover {
            color: $main-red; } }
    &__image {
        width: 270px;
        height: 230px; }

    & .icon {
        position: absolute;
        top: 178px;
        left: 15px;
        width: 37px;
        height: 37px;
        background: radial-gradient(circle closest-side at 18.5px 18.5px, rgba(255, 255, 255, 1) 80%, rgba(255, 0, 0, 0) ); }

    &__bottom {
        width: 270px;
        height: 46px;
        margin-top: 30px;

        & a {
            font-family: $font-family-h;
            font-weight: 500;
            font-size: 18px;
            line-height: 130%;
            letter-spacing: 0.01em;
            color: $main-blue; } } }


.block__main-callback {
    & .block__content {
        display: flex;
        align-items: flex-end;
        justify-content: flex-end;
        height: 600px;
        padding-right: 70px;
        background-image: url("../images/section-callback-img.png");
        background-color: #E1E1E1;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover; } }

.callback-form {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 30px;
    height: 503px;
    background: #FFFFFF;

    & h2 {
        font-size: 25px;
        line-height: 135%;
        letter-spacing: 0.0238em;
        color: $main-blue;

        span {
            color: #1C3143; } }

    & p {
        margin: 3px 0 0 0;
        font-size: 17px;
        line-height: 170%;
        color: #555555; }

    & form {
        display: flex;
        flex-direction: column;
        width: 100%;
        gap: 30px;

        & input {
            width: 100%;
            height: 44px;
            padding-left: 10px;
            border: none;
            border-bottom: 1px solid $main-blue; } }


    & .btn {
        margin-top: 18px;
        padding: 0;
        background: transparent;
        display: flex;
        align-items: center;
        border: 0;
        gap: 15px;
        font-family: 'Oswald';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 165%;
        color: $main-blue;
        letter-spacing: 0.05em;
        cursor: pointer;

        &:hover {
            color: $main-red; }

        & .icon {
            width: 37px;
            height: 37px; } } }

@include media-breakpoint-up(xxl) {

    .block {
        &__header-left,
        &__content-left {
            width: 365px; }

        &__product {
            & .block__content {
                gap: 35px; } }

        &__about-company {
            min-height: 426px; } }

    .product-item {
        width: 330px;

        &_dark {
            & .product-item__main {
                height: 230px; } }

        &_light {
            & .product-item__main {
                height: 240px; } } }

    .callback-form {
        width: 625px;
        padding: 70px 70px 0px; } }

@include media-breakpoint-up(xl) {

    .block {
        margin-top: 150px; }

    .product-item {
        &__main {}

        &__surface {
            padding: 15px; }

        & h3 {
            margin-top: 50px; }

        &__description {
            margin-top: 30px; } } }


@include media-breakpoint-only(xl) {
    .block {
        &__header-left,
        &__content-left {
            width: 306px; }

        &__product {
            & .block__content {
                gap: 30px; } }

        &__about-company {
            min-height: 484px; } }

    .product-item {
        width: 273.33px;

        &_dark {
            & .product-item__main {
                height: 240px; } }

        &_light {
            & .product-item__main {
                height: 230px; } } }

    .callback-form {
        width: 505px;
        padding: 70px 30px 0px; } }

@include media-breakpoint-up(lg) {

    .block {
        &__product {
            & .block__content {
                margin-top: 65px; } }

        &__application {
            & .container {
                margin-top: 74px; } } } }

@include media-breakpoint-only(lg) {

    .block {
        margin-top: 80px;

        &__header-left,
        &__content-left {
            width: 236px; }

        &__product {
            & .block__content {
                gap: 20px; } }

        &__about-company {
            min-height: 574px; } }

    .product-item {
        width: 216px;

        &__surface {
            padding: 15px 10px; }

        & h3 {
            margin-top: 20px; }

        &__description {
            margin-top: 20px; }

        &__bottom {
            padding: 0 10px; }

        &__link {
            gap: 7px; }

        & .icon {
            width: 25px;
            height: 25px; }

        &_dark {
            & .product-item {
                &__main {
                    height: 172px; }

                &__bottom {
                    height: 65px; } } }

        &_light {
            & .product-item__main {
                height: 230px; } } }

    .block__main-callback {
        & .block__content {
            height: 450px; } }

    .callback-form {
        width: 518px;
        height: 393px;
        padding: 30px 30px 0px;
        gap: 20px;

        & form {
            gap: 20px; }

        & .btn {
            margin-top: 9px; } } }

@include media-breakpoint-only(md) {

    .block {
        margin-top: 50px;

        &__header {
            flex-direction: column;
            gap: 10px; }


        &__header-left {
            padding-top: 10px;
            width: 236px; }

        &__content-left {
            width: 0px; }

        &__product {
            & .block__content {
                margin-top: 50px;
                gap: 20px; } }

        &__application {
            height: 444px;

            & .block__content {
                top: 60px; } }

        &__about-company {
            min-height: 679px;
            margin-top: 75px;

            & .block__header {
                gap: 45px; } } }

    .product-item {
        width: 210px;

        &__surface {
            padding: 15px 10px; }

        & h3 {
            margin-top: 20px; }

        &__description {
            margin-top: 20px; }

        &__bottom {
            padding: 0 10px; }

        &__link {
            gap: 7px; }

        & .icon {
            width: 20px;
            height: 20px; }

        &_dark {
            & .product-item {
                &__main {
                    height: 172px; }

                &__bottom {
                    height: 65px; } } }

        &_light {
            & .product-item {
                &__main {
                    height: 172px; }

                &__bottom {
                    height: 65px; } } } }

    .block__main-callback {
        margin-top: 84px;
        & .block__content {
            height: 450px;
            padding: 38px 20px 0; } }

    .callback-form {
        width: 100%;
        height: 100%;
        padding: 20px 20px 0px;
        gap: 20px;

        & form {
            gap: 20px; }

        & .btn {
            margin-top: 9px; } } }

@include media-breakpoint-down(md) {

    .block {
        margin-top: 40px;

        & .container {
            width: 280px; }

        &__header {
            flex-direction: column;
            gap: 10px; }


        &__header-left {
            padding-top: 0;
            line-height: 18px; }

        &__header-left {
            width: 236px; }

        &__sub-title {
            line-height: 110%; }

        &__content-left {
            width: 0px; }

        &__product {
            & .block__content {
                margin-top: 50px;
                gap: 20px; } }

        &__application {
            height: 444px;

            & .block__content {
                top: 60px; } }

        &__about-company {
            margin-top: 70px;

            & .container {
                width: 100%; }

            & .block__header {
                gap: 45px; } } }

    .product-item {
        width: 210px;

        &__surface {
            padding: 15px 10px; }

        & h3 {
            margin-top: 20px; }

        &__description {
            margin-top: 20px; }

        &__bottom {
            padding: 0 10px; }

        &__link {
            gap: 7px; }

        & .icon {
            width: 20px;
            height: 20px; }

        &_dark {
            & .product-item {
                &__main {
                    height: 172px; }

                &__bottom {
                    height: 65px; } } }

        &_light {
            & .product-item {
                &__main {
                    height: 172px; }

                &__bottom {
                    height: 65px; } } } }

    .block__main-callback {
        margin-top: 90px;
        & .block__content {
            width: 100%;
            height: 546px;
            padding: 20px 10px 0; } }

    .callback-form {
        width: 100%;
        height: 100%;
        padding: 20px 20px 0px;
        gap: 20px;

        & form {
            gap: 20px; }

        & .btn {
            margin-top: 9px;

            span {
                text-align: left; } } } }
