.header__search {
    position: relative;
    min-width: 29.37px;

    &-box {
        position: absolute;
        z-index: 1; }

    & input {
        height: 100%;
        background: $white;
        border: none;
        outline: none; } }

@include media-breakpoint-up(xl) {
    .header__search {

        &-box {
            right: 0px;
            top: 0px;
            width: 29px;
            height: 29px;
            border-radius: 14px;

            &--focused {
                width: 264px;
                border: 1px solid $main-blue; }

            &.is-invalid {
                border: 1px solid $main-red; } }

        & input {
            width: calc(100% - 30px);
            padding-left: 13px;
            border-radius: 14px; }

        & .btn {
            position: relative;
            margin-left: 2px;
            padding: 0;
            border: none;
            border-radius: 50%;
            color: $main-blue-dark;
            z-index: 2;

            & .icon {
                width: 24px;
                height: 27px; }

            &--focused {
                margin-left: 0;
                transition: .3s;

                & .icon {
                    width: 18px;
                    transition: .3s; } } } } }

@include media-breakpoint-down(xl) {
    .header__search {
        &-box {
            top: 0px;
            width: 0px;
            border-radius: 14px 0px 0px 14px;
            overflow: hidden;

            &--focused {
                border: 1px solid $main-blue; }

            &.is-invalid {
                border: 1px solid $main-red; } }

        & input {
            width: calc(100% - 3px); }

        & .btn {
            position: relative;
            z-index: 2;

            & .icon {
                width: 33px;
                height: 33px; } } } }

@include media-breakpoint-only(lg) {
    .header__search {
        &-box {
            right: 56px;
            height: 50px;

            &--focused {
                width: 314px; } }

        & input {
            padding: 0 10px 0 17px; }

        & .btn {
            & .icon {
                width: 33px;
                height: 33px; } } } }

@include media-breakpoint-only(md) {
    .header__search {
        &-box {
            right: 47px;
            height: 45px;

            &--focused {
                width: 314px; } }

        & input {
            padding: 0 10px 0 20px; } } }

@include media-breakpoint-down(md) {
    .header__search {
        &-box {
            right: 37px;
            height: 35px;

            &--focused {
                width: 208px; } }

        & input {
            padding: 0 10px 0 12px; } } }
